@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
html, body, #home {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
  height: 100vh;
  color: #333;
  background-color: #fafbfc;
}

body {
  background-attachment: fixed;
}
/* HOME */
#home {
  z-index: 10;
  background: linear-gradient(0deg, rgba(7,9,28,1) 19%, rgba(0,0,0,1) 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  
}
#home .apresentacao h1, h2, h3, p, a, hr {
  margin: 0;
  z-index: 10;
}
#home .apresentacao h1, h2, h3 {
  color: #DDD;
}
#home .apresentacao hr {
  border-top: 1px solid rgba(255,255,255,.1);
}
#home .apresentacao h2 {
  font-size: 18px;
}
#home .apresentacao p {
  color: #bbb;
  margin-bottom: 4px;
}
#home .links {
  width: 100%;
  display: flex;
  margin: 10px 0;
}
#home .links a {
  color: #DDD;
  width: 50px;
  height: 50px;
  text-align: center;
  font-size: 30px;
  border-radius: 50%;
  border: 2px solid #DDD;
  margin-right: 1vw;
  transition: all 0.5s;
}
#home .links a:hover {
  text-decoration: none;
  background-color: #DDD;
  color: #444;
  border: 2px solid #333;
}
#home .content-home {
  width: 45vw;
  display: flex;
  align-items: center;
}

#home .content-ul ul{
  padding: 0;
  list-style-type: none;
  display: flex;
  
}
#home .content-ul ul li {
  margin: 20px;
}
#home .content-ul ul li a {
  color: #DDD;
  font-size: 17px;
}
#home .content-ul ul li a:hover {
  text-decoration: none;
  color: #fff;
  font-weight: bold;

}
.foto-perfil img{
  z-index: 10;
  width: 132px;
  border-radius: 50%;
  margin-right: 17px;
  border: 3px solid #CCC;
}
section {
  margin: 10px auto;
}

/* ABOUT */
#about h1 {
  margin: 20px;
}
#about {
  height: 100vh;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#about .container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
#about .container .timeline {
  width: 70%;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}
.direita {
  width: 48%;
  align-self: flex-end;
  text-align: left;
}
.esquerda {
  align-self: flex-start;
  width: 48%;
  text-align: right;
}
/* SKILLS */
#skills {
  color: #333;
}
#skills h1 {
  width: 100%;
}
#skills {
  padding: 5vh 20vw;
  width: 100%;
  display:flex;
  flex-wrap: wrap;
  justify-content: space-around;
  
  background-color: #fff;
  background-clip: border-box;
  border: 1px solid rgba(0,0,0,.125);
}
#skills .card {
  width: 30%;

}
#skills .card .card-body {
  text-align: center;
}
#skills .card .card-body ul {
  padding: 0;
  list-style-type: none;
}
#skills .card .card-body .icon {
  font-size: 56px;
}
#skills .card .card-header {
  background-color: #FFF;
  border-bottom: none;
  text-align: center;
}
#skills .card .card-header h2 {
  color: #333;
}
/* PROJECTS */
#projects {
  /* height: 100vh; */
  padding: 5vh 20vw;
  width: 100%;
  display:flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
#projects h1 {
  width: 100%;
}
#projects .card {
  width: 45%;
  margin: 20px auto;
}
#projects .card .card-header {
  background-color: #FFF;
  border-bottom: 0;
}
#projects .card .card-header h2  {
  font-size: 22px;;
  color: #222;
}
#projects .card .card-header h4  {
  font-size: 16px;
  color: #555;
}
#projects .card .card-body {
  padding-top: 0;
}
#projects .card .card-footer {
  background-color: #FFF;
}
#projects .card .card-footer .links {
  display:flex;
  justify-content: flex-end;
  align-items: center;
}
#projects .card .card-footer .links a {
  font-size: 16px;
  line-height: 25px;
  color: #444;
  transition: all 0.4s;
}
#projects .card .card-footer .links a i {
  font-size: 25px;
}
#projects .card .card-footer .links a:hover {
  color: #111;
}





@media (max-width: 1366px) {
  #about{
    height: auto;
  }
}
@media (max-width: 1200px) {
  #home .content-home {
    width: 50vw;
  }
  #home .apresentacao h3 {
    font-size: 15px;
  }
  
  #skills .card {
    width: 45%;
    margin: 5px;
  }
  #about .container .timeline {
    width: 100%;
  }
  .direita {
    width: 50%;
  }
  .esquerda {
    width: 50%;
  }
}
@media (max-width: 992px) {
  #home .content-home {
    flex-direction: column;
  }
  #home .apresentacao h3 {
    font-size: 22px;
  }
  #skills .card {
    width: 100%;
  }
  #skills {
    flex-direction: column;
  }
  #projects .card {
    width: 75%;
  }
}
@media (max-width: 768px) {
  #home .content-home {
    width: 70vw;
  }
  #about .container .timeline {
    width: 100%;
  }
  .direita {
    width: 100%;
    align-self: flex-start;
    text-align: justify;
  }
  .esquerda {
    width: 100%;
    align-self: flex-start;
    text-align: justify;
  }
  #skills {
    padding: 2vh 5vw;
  }
  #projects .card {
    width: 100%;
  }
  #projects {
    flex-direction: column;
    padding: 3vh 5vw;
  }
  
}
@media (max-width: 576px) {
  #home .content-home {
    width: 90vw;
  }
}
